export default {
	data() {
		return {
			index:0,
			number:6
		}
	},
	onLoad() {

	},
	methods: {
		tabchange(idx){
			this.index=idx
			if(idx==1){
				this.number=12
			}else{
				this.number=6
			}
		}
	},
	onPullDownRefresh() {
		let self = this
		setTimeout(function () {
	        uni.stopPullDownRefresh();
			self.index=0
			self.number=6
	    }, 1000);
	},
}
